* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "PT Sans", "Arial", sans-serif;
}
@font-face {
  font-family: "PT Sans";
  font-weight: normal;
  font-style: normal;
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  font-weight: bold;
  font-style: normal;
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
}
.container {
  margin: 0 auto;
  max-width: 1100px;
}
.body {
  margin: 0;
  padding: 0;
  font-family: "PT Sans", "Arial", sans-serif;
  min-height: 100vh;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: min-content 1fr min-content;
}
.body--scrolloff {
  overflow: hidden;
}
.standart-arrow-buton {
  width: 50px;
  height: 50px;
  background: linear-gradient(87.32deg, #E2A300 4.47%, #E29500 95.53%);
  border: none;
  border-radius: 50%;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
}
.standart-arrow-buton:hover,
.standart-arrow-buton:focus {
  opacity: 0.7;
}
.standart-arrow-buton:active {
  opacity: 0.3;
}
html {
  scroll-behavior: smooth;
}
.paralax {
  height: 300px;
  position: relative;
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: url('../img/paralax/paralax_4.jpg');
}
.paralax::after {
  content: '';
  bottom: -1px;
  left: 0;
  position: absolute;
  width: 100%;
  height: 50px;
  background: linear-gradient(0deg, #414141 0%, rgba(65, 65, 65, 0) 100%);
}
.paralax::before {
  content: '';
  top: -1px;
  left: 0;
  position: absolute;
  width: 100%;
  height: 50px;
  background: linear-gradient(0deg, #414141 0%, rgba(65, 65, 65, 0) 100%);
  transform: rotate(180deg);
}
.paralax--stack {
  height: 250px;
}
.paralax--stack::after {
  height: 75px;
}
.paralax--stack::before {
  display: none;
}
.paralax--about-me {
  height: 250px;
  background-image: url('../img/paralax/paralax_1.jpg');
}
.paralax--portfolio {
  height: 250px;
  background-image: url('../img/paralax/paralax_2.jpg');
}
.paralax--portfolio::after {
  display: none;
}
.paralax--portfolio::before {
  height: 40px;
}
.paralax--footer {
  height: 200px;
  background-image: url('../img/paralax/paralax_3.jpg');
}
.paralax--footer::after {
  height: 40px;
}
.paralax--footer::before {
  display: none;
}
.paralax--all-projects {
  height: 400px;
}
.paralax__blur {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  backdrop-filter: blur(3px);
}
.paralax__title {
  z-index: 1;
  font-weight: bold;
  font-size: 34px;
  color: #E4E4E4;
  text-transform: uppercase;
  text-align: center;
  font-family: 'Roboto Mono', "Arial", monospace;
  line-height: 30px;
  width: 1px;
  opacity: 0;
  overflow: hidden;
}
.paralax__title--show {
  width: 350px;
  opacity: 1;
  transition: opacity 0s, width 2s;
}
.header {
  position: fixed;
  z-index: 3;
  top: 0;
  left: 0;
  width: 100%;
  background: linear-gradient(101.56deg, #2C2C2C 1.56%, #1C1C1C 95.55%);
  opacity: 0.95;
  padding: 20px;
  border-bottom: 2px solid #00C1EC;
}
@media (max-width: 900px) {
  .header {
    padding: 5px 20px;
  }
}
.header__wrap {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__title {
  font-family: 'Hammersmith One', sans-serif;
  font-weight: normal;
  font-size: 22px;
  line-height: 18px;
  color: #00C1EC;
}
.hed-portfolio__back-icon {
  transform: rotate(180deg);
}
.nav {
  width: min-content;
}
.nav__btn-wrap {
  display: none;
}
@media (max-width: 900px) {
  .nav__btn-wrap {
    display: block;
  }
}
.nav__btn {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 80px;
  height: 80px;
  transition: all 0.5s ease-in-out;
  border: none;
  background-color: transparent;
  margin: 0;
  padding: 0;
  z-index: 1;
  cursor: pointer;
}
.nav__btn-burger {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50px;
  height: 6px;
  background-color: #fff;
  border: none;
  border-radius: 5px;
  transition: all 0.5s ease-in-out;
}
.nav__btn-burger::before,
.nav__btn-burger::after {
  content: '';
  position: absolute;
  width: 50px;
  height: 6px;
  background-color: #fff;
  border-radius: 5px;
  transition: all 0.5s ease-in-out;
}
.nav__btn-burger--open {
  background-color: #ffffff;
}
.nav__btn-burger--open::before,
.nav__btn-burger--open::after {
  background-color: #ffffff;
}
.nav__btn-burger::before {
  transform: translateY(-16px);
}
.nav__btn-burger::after {
  transform: translateY(16px);
}
.nav__btn--open .nav__btn-burger {
  transform: translateX(-50px);
  background: transparent;
}
.nav__btn--open .nav__btn-burger::before {
  transform: rotate(45deg) translate(35px, -35px);
}
.nav__btn--open .nav__btn-burger::after {
  transform: rotate(-45deg) translate(35px, 35px);
}
.nav__list {
  display: block;
  width: 100%;
  height: 100%;
}
@media (max-width: 900px) {
  .nav__list {
    transition: all 0.5s ease-in-out;
    opacity: 0;
    pointer-events: none;
  }
}
.nav__list--open {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  overflow-y: scroll;
  transition: all 0.5s ease-in-out;
  opacity: 1;
  pointer-events: visible;
}
.nav__background-notouch {
  display: none;
}
.nav__background-notouch--on {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.7);
}
.nav__list-wrap {
  display: grid;
  grid-template-columns: repeat(6, min-content);
  grid-column-gap: 20px;
  width: 100%;
  height: 100%;
}
@media (max-width: 900px) {
  .nav__list-wrap {
    position: absolute;
    top: 0;
    left: 0;
    padding: 80px 10px 20px 10px;
    width: 100%;
    height: min-content;
    border-bottom: 4px solid #00C1EC;
    background: linear-gradient(101.56deg, #2C2C2C 1.56%, #1C1C1C 95.55%);
    color: #ffffff;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
  }
}
.nav__elem {
  list-style: none;
}
@media (max-width: 900px) {
  .nav__elem {
    margin-bottom: 20px;
    padding: 10px;
    width: 100%;
    text-align: center;
  }
}
.nav__link {
  padding: 7px 0;
  font-family: 'Work Sans', sans-serif;
  color: #E4E4E4;
  text-decoration: none;
  font-weight: 300;
  font-size: 16px;
  line-height: 21px;
  white-space: pre;
  position: relative;
}
.nav__link:hover,
.nav__link:focus {
  opacity: 0.9;
  text-decoration: underline;
  text-decoration-thickness: 2px;
  color: #E2A300;
}
.nav__link:active {
  opacity: 0.7;
  text-decoration: underline;
  text-decoration-thickness: 2px;
  color: #E2A300;
}
@media (max-width: 900px) {
  .nav__link {
    display: inline-block;
    font-weight: 600;
    font-size: 22px;
    width: 100%;
    border-bottom: 4px solid transparent;
    text-transform: uppercase;
  }
  .nav__link:hover,
  .nav__link:focus {
    opacity: 0.9;
    text-decoration: none;
    border-bottom: 4px solid #00C1EC;
  }
}
.main {
  position: relative;
  width: 100%;
  height: 100%;
  background: linear-gradient(130deg, #414141 0%, #2D2D2D 100.52%);
}
.hello {
  width: 100%;
