  padding: 20px 20px 0px 20px;
  background: linear-gradient(101.56deg, #2C2C2C 1.56%, #1C1C1C 95.55%);
}
.hello__wrap {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
@media (max-width: 900px) {
  .hello__wrap {
    flex-direction: column;
    justify-content: center;
    position: relative;
  }
}
@media (max-width: 700px) {
  .hello__wrap {
    align-items: flex-end;
  }
}
.hello__info {
  z-index: 2;
  width: 350px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  color: #E4E4E4;
}
@media (max-width: 900px) {
  .hello__info {
    align-items: center;
    text-align: center;
    width: 450px;
  }
}
@media (max-width: 700px) {
  .hello__info {
    width: 70%;
    padding: 0 10px;
    align-items: flex-end;
    justify-content: center;
  }
}
.hello__subtitle {
  margin-bottom: 27px;
  font-family: "PT Sans", "Arial", sans-serif;
  font-weight: 600;
  font-size: 48px;
  line-height: 54px;
}
