    margin-bottom: 16px;
  }
}
@media (max-width: 700px) {
  .hello__subtitle {
    font-size: 28px;
    line-height: 20px;
  }
}
.hello__title {
  margin-bottom: 27px;
  font-family: "PT Sans", "Arial", sans-serif;
  font-weight: bold;
  font-size: 72px;
  line-height: 82px;
}
@media (max-width: 900px) {
  .hello__title {
    margin-bottom: 16px;
  }
}
@media (max-width: 700px) {
  .hello__title {
    font-size: 52px;
    line-height: 62px;
    text-align: right;
  }
}
.hello__text {
  margin-bottom: 27px;
  font-family: 'Roboto Mono', monospace, sans-serif;
  font-weight: 300;
  font-size: 18px;
  line-height: 21px;
}
@media (max-width: 900px) {
  .hello__text {
    margin-bottom: 16px;
  }
}
@media (max-width: 700px) {
  .hello__text {
    font-size: 16px;
    line-height: 18px;
    text-align: right;
  }
}
.hello__button {
  padding: 14px 40px;
  background: linear-gradient(87.32deg, #E2A300 4.47%, #E29500 95.53%);
  box-shadow: 0px 2px 13px rgba(226, 158, 0, 0.48);
  border: none;
  border-radius: 6px;
  font-style: normal;
  font-weight: bold;
  font-size: 18px;
  line-height: 20px;
  text-transform: uppercase;
  font-family: "PT Sans", "Arial", sans-serif;
  color: #000000;
  cursor: pointer;
  transition: 0.4s;
}
@media (max-width: 900px) {
  .hello__button {
    width: 80%;
  }
}
@media (max-width: 700px) {
  .hello__button {
    width: auto;
  }
}
.hello__button:hover,
.hello__button:focus {
  box-shadow: 0px 2px 13px 7px rgba(226, 158, 0, 0.48);
}
.hello__pic {
  position: relative;
  height: 100%;
  width: 575px;
  flex-grow: 1;
  display: flex;
  justify-content: center;
  align-items: flex-end;
}
@media (max-width: 900px) {
  .hello__pic {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
}
.hello__pic::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
  height: 700px;
  border-radius: 50%;
  border: 67px solid #00C4F0;
  box-sizing: border-box;
  filter: drop-shadow(0px 0px 152px rgba(0, 196, 240, 0.42));
}
@media (max-width: 900px) {
  .hello__pic::after {
    height: 100%;
  }
}
@media (max-width: 700px) {
  .hello__pic::after {
    left: -30%;
  }
}
.hello__pic-wrap {
  position: relative;
  z-index: 1;
  height: 700px;
  width: 450px;
}
@media (max-width: 900px) {
  .hello__pic-wrap {
    height: 100%;
    width: 100%;
  }
}
.hello__img-scills {
  width: 90px;
  height: 90px;
  border-radius: 50%;
  background: linear-gradient(130.68deg, #414141 0%, #2D2D2D 100.52%);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1;
  position: absolute;
}
@media (max-width: 700px) {
  .hello__img-scills {
    width: 60px;
    height: 60px;
  }
  .hello__img-scills > svg {
    width: 30px;
    height: 30px;
  }
}
.hello__img-scills--one {
  bottom: 80%;
  left: -15%;
}
@media (max-width: 900px) {
  .hello__img-scills--one {
    left: 5%;
  }
}
@media (max-width: 700px) {
  .hello__img-scills--one {
    left: 0;
  }
}
.hello__img-scills--two {
  bottom: 20%;
  left: -20%;
}
@media (max-width: 900px) {
  .hello__img-scills--two {
    left: 0;
    bottom: 20%;
  }
}
@media (max-width: 700px) {
  .hello__img-scills--two {
    bottom: 15%;
    left: 2%;
  }
}
.hello__img-scills--thre {
  left: 95%;
  bottom: 70%;
}
@media (max-width: 900px) {
  .hello__img-scills--thre {
    left: 88%;
  }
}
@media (max-width: 700px) {
  .hello__img-scills--thre {
    left: 10%;
    bottom: 50%;
  }
}
.hello__iam-img {
  height: 100%;
  width: 100%;
}
@media (max-width: 900px) {
  .hello__iam-img {
    display: none;
  }
}
.stack {
  width: 100%;
  padding: 20px;
  background-color: #414141;
  display: flex;
  justify-content: center;
  align-items: center;
}
.stack__list {
  width: 700px;
  margin: 50px 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
@media (max-width: 900px) {
  .stack__list {
    width: 100%;
    margin: 20px 0;
  }
}
.stack__item {
  position: relative;
  width: 150px;
  height: 150px;
  padding: 15px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  cursor: pointer;
  background-color: #2A2A2A;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.47);
  border-radius: 29px;
  margin: 0 12.5px 25px 12.5px;
  transition: 0.3s all;
}
.stack__item:hover {
  box-shadow: 0px 2px 10px 4px rgba(0, 0, 0, 0.47);
}
.stack__item:hover .stack__info,
.stack__item:focus .stack__info {
  visibility: visible;
  opacity: 1;
  transform: translate(-50%, -25%);
  pointer-events: visible;
}
.stack__item:hover > .stack__img {
  width: 65px;
  height: 65px;
}
@media (max-width: 700px) {
  .stack__item {
    width: 100px;
    height: 100px;
    padding: 10px;
  }
}
.stack__info {
  position: absolute;
  z-index: 1;
  top: -10%;
  left: 50%;
  width: 140px;
  height: min-content;
  padding: 10px;
  border-radius: 29px;
  border: 1px solid #00c4f0;
  background-color: #2A2A2A;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.47);
  text-align: center;
  color: #ffffff;
  transition: 0.3s all;
  visibility: hidden;
  opacity: 0;
  transform: translate(-50%, 100%);
  pointer-events: none;
}
.stack__name {
  font-weight: 300;
  font-size: 16px;
  line-height: 29px;
  color: #E4E4E4;
  font-family: 'Roboto Mono', monospace, sans-serif;
}
@media (max-width: 700px) {
  .stack__name {
    font-size: 13px;
    line-height: 14px;
  }
}
.stack__img {
  position: absolute;
  top: 45%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: 0.3s all;
  width: 50px;
  height: 50px;
}
@media (max-width: 900px) {
  .stack__img {
    width: 35px;
    height: 35px;
  }
}
.about-me {
  width: 100%;
  padding: 100px 20px;
  background-color: #414141;
  display: flex;
  justify-content: center;
  align-items: center;
}
.about-me__wrap {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  justify-content: center;
}
@media (max-width: 900px) {
  .about-me__wrap {
    width: 100%;
    grid-template-columns: min-content 1fr;
    grid-template-rows: 1fr min-content;
  }
}
@media (max-width: 700px) {
  .about-me__wrap {
    grid-template-columns: 1fr;
    grid-template-rows: repeat(3, min-content);
  }
}
.about-me__article {
  z-index: 2;
  position: relative;
  order: 2;
  width: 370px;
  height: 100%;
  background: #2C2C2C;
  border-radius: 14px;
  padding: 30px 40px;
}
@media (max-width: 900px) {
  .about-me__article {
    width: 100%;
    padding: 20px 15px;
  }
}
.about-me__text {
  font-style: normal;
  font-weight: 300;
  font-size: 20px;
  line-height: 24px;
  color: #ffffff;
}
.about-me__pic {
  z-index: 1;
  position: relative;
  display: flex;
}
.about-me__pic--double {
  order: 1;
  height: 100%;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-end;
}
.about-me__pic--double::after {
  z-index: -1;
  position: absolute;
  content: '';
  width: 400px;
  height: 600px;
  left: -50px;
  top: 50px;
  border-radius: 50%;
  border: 89px solid #E4E4E4;
  filter: drop-shadow(0px 4px 70px rgba(255, 255, 255, 0.25));
}
@media (max-width: 900px) {
  .about-me__pic--double {
    z-index: 2;
    order: 3;
    grid-column-start: 1;
    grid-column-end: 3;
    justify-content: space-between;
    flex-direction: row;
    width: 100%;
  }
  .about-me__pic--double::after {
    top: 0;
    left: 0;
    height: 150px;
    width: 75%;
  }
}
@media (max-width: 700px) {
  .about-me__pic--double {
    grid-column-start: 1;
    grid-column-end: -1;
  }
  .about-me__pic--double::after {
    display: none;
  }
}
.about-me__pic--solo {
  order: 3;
  align-items: center;
}
.about-me__pic--solo::before {
  z-index: -1;
  position: absolute;
  content: '';
  width: 400px;
  height: 400px;
  right: 20px;
  top: 50%;
  border-radius: 50%;
  border: 55px solid #00C4F0;
  box-sizing: border-box;
  filter: drop-shadow(0px 4px 70px rgba(0, 193, 236, 0.25));
}
.about-me__pic--solo::after {
  z-index: -1;
  position: absolute;
  content: '';
  width: 300px;
  height: 300px;
  right: 120px;
  top: 0;
  border-radius: 50%;
  border: 34px solid #E2A200;
  box-sizing: border-box;
  filter: drop-shadow(0px 4px 70px rgba(0, 193, 236, 0.25));
}
@media (max-width: 900px) {
  .about-me__pic--solo {
    order: 1;
    justify-content: flex-end;
    width: 250px;
    margin-right: -10px;
  }
  .about-me__pic--solo::after {
    top: 60%;
    left: -5%;
  }
  .about-me__pic--solo::before {
    top: -10%;
    left: -30%;
  }
}
@media (max-width: 700px) {
  .about-me__pic--solo {
    width: 100%;
    height: 250px;
    justify-content: center;
  }
  .about-me__pic--solo::after {
    width: 230px;
    height: 230px;
    top: 50%;
    left: 0;
  }
  .about-me__pic--solo::before {
    top: 20%;
    left: 100%;
    transform: translateX(-100%);
    width: 260px;
    height: 250px;
    border: 40px solid #00C4F0;
  }
}
.about-me__img-wrap {
  padding: 20px;
  background: #2C2C2C;
  border-radius: 14px;
  position: relative;
  z-index: 1;
}
.about-me__img-wrap--double {
  margin-right: -20px;
}
.about-me__img-wrap--double:not(:last-child) {
  margin-top: -20px;
}
@media (max-width: 900px) {
  .about-me__img-wrap--double {
    width: 100%;
    margin-right: 0;
