}
.slider-controls {
  height: min-content;
  position: relative;
  width: min-content;
  display: grid;
  grid-template-columns: repeat(2, min-content);
  align-items: center;
  grid-column-gap: 20px;
}
.slider-controls::before {
  content: '';
  position: absolute;
  width: 4px;
  height: 100%;
  right: 50%;
  transform: translateX(100%) rotate(15deg);
  top: 0;
  background-color: #ffffff;
}
